@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400;1,500;1,600&family=Playfair+Display:wght@400;500;600&display=swap');

:root{
  --slider-section-height: 600px;
  --feature-section-height: 710px;
}

*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: 'Open Sans', sans-serif;
}

*::selection{
  background-color: #333333;
  color: white;
}

ul{
  list-style: none;
}

.container{
  width: 100%;
  position: relative;
}

.slider{
  position: fixed;
  z-index: -1;
  height: var(--slider-section-height);
  width: 100%;
  top: 0;
}

/*HEADER*/

.header-wrapper{
  width: 100%;
  display: flex;
  justify-content: center;
}

.header{
  min-width: 1140px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 62px;
  position: fixed;
  z-index: 1;
}

.header__logo{
  width: 125px;
  height: 80px;
  text-align: center;
  line-height: 80px;
  text-decoration: none;
  color: white;
  font-size: 1.7rem;
  text-transform: uppercase;
  font-weight: 700;
  transition: all 0.4s ease;
}

.header__logo:hover{
  color: #999;
}

.header-facilities {
  display: flex;
  min-width: 600px;
  font-size: 0.75rem;
}

/*NAVBAR*/

.navbar{
  flex: 1;
  display: flex;
  justify-content: space-evenly;
}

.navbar-items{
  font-weight: 700;
  text-decoration: none;
  color: #999;
  text-transform: uppercase;
  letter-spacing: 1px;
  position: relative;
  transition: all 0.4s ease;
}

.navbar-items::before {
  content: '';
  position: absolute;
  top: 14px;
  left: 0px;
  width: 50px;
  height: 20px;
  background-color: transparent;
}

.navbar-items:hover{
  color: white;
  cursor: pointer;
}

.navbar-items:hover > .subnav{
  display: block;
  opacity: 1;
  transition: opacity 0.5s ease-in-out;
}

.navbar-items:hover > .subnav--padding{
  display: grid;
  opacity: 1;
}

.navbar-items--active{
  color: white;
}

/*SUBNAV*/

.subnav{
  list-style: none;
  opacity: 0;
  position: absolute;
  background-color: #1f1f1f;
  top: 34px;
  left: -12px;
  min-width: 180px;
  display: none;
}

.subnav-items{
  display: flex;
  font-size: 0.7rem;
  text-transform: capitalize;
  padding: 10px 15px;
  color: #999;
  font-weight: lighter;
  justify-content: space-between;
  align-items: center;
}

.subnav-items:hover{
  color: white;
  background-color: #000;
  transition: all 0.4s ease-out;
}

.subnav--padding{
  display: none;
  opacity: 0;
  grid-template-columns: repeat(3,390px);
  position: absolute;
  top: 34px;
  left: -745px;
  background-color: #1f1f1f;
  padding: 20px;
}

.subnav__heading{
  margin: 0 0 8px 32px;
  font-size: 0.8rem;
  color: #999;
}

.subnav__heading:hover{
  color: white;
}

.subnav__col1 ul{
  margin-right: 18px;
}

.subnav__col1 .subnav__heading{
  margin: 0 0 8px 15px;
}

.subnav__col2{
  border-left: 1px solid #ffffff1f;
  border-right: 1px solid #ffffff1f;
}

.subnav__col2 ul{
  margin: 0 18px;
}

.subnav__col3 ul{
  margin-left: 18px;
}

.subnav-items-icon {
  font-size: 0.5rem;
}

.sub-subnav{
  position: absolute;
  display: none;
  width: 190px;
  left: -190px;
  top: 0;
  background: #1f1f1f;
}

.subnav-items:hover > .sub-subnav{
  display: block;
}

.cart{
  color: #999;
  position: relative;
}

.cart::before {
  content: '3';
  position: absolute;
  top: -7px;
  right: -11px;
  width: 15px;
  height: 15px;
  background-color: #2d66f7;
  border-radius: 50%;
  text-align: center;
  line-height: 15px;
  color: white;
}

.cart:hover{
  color: white;
  cursor: pointer;
  transition: all 0.4s ease;
}

.cart::after{
  content: "";
  position: absolute;
  top: 15px;
  right: -9px;
  background-color: transparent;
  width: 50px;
  height: 20px;
  border-radius: 0;
}

.cart-icon{

}

.search {
  margin-left: 40px;
  color: #999;
}

.search:hover{
  color: white;
  cursor: pointer;
  transition: all 0.4s ease;
}

.subnav--padding.subnav--center-position {
  left: -970px;
}

.subnav--padding.subnav--center-position .subnav-items{
  align-items: center;
  justify-content: flex-start;
}

.subnav--padding.subnav--center-position .subnav-items-icon{
  font-size: 0.7rem;
  margin-right: 12px;
  line-height: 12px;
}

.search-icon{

}

.bar{
}

.bar-icon{

}

.body{
  position: absolute;
  top: 600px;
  width: 100%;
  overflow: hidden;
}

.desc {
  position: absolute;
  top: 50%;
  width: 100%;
  transform: translateY(-50%);
  text-align: center;
}

.desc__img{
  filter: brightness(0.3);
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.desc__heading{
  font-family: 'Playfair Display', serif;
  font-size: 5rem;
  text-transform: capitalize;
  color: white;
  font-weight: 400;
  position: relative;
}

.desc__heading::before {
  content: 'v.1.0.1';
  position: absolute;
  top: -24px;
  right: 31%;
  color: #999;
  font-size: 1.6rem;
  text-transform: lowercase;
}

.desc__words{
  color: white;
  margin: 4px 0 36px;
  font-size: 1.3rem;
  font-weight: lighter;
}

/*BUTTON*/

.btn{
  background: none;
  outline: none;
  padding: 14px 28px;
  border-radius: 40px;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}

.btn--transparent{
  border: 1px solid white;
  color: white;
}

.btn--white:hover,
.btn--black:hover,
.btn--transparent:hover,
.footer-content__btn:hover{
  border: 1px solid #2d66f7 ;
  background-color: #2d66f7 ;
}

.btn--white{
  border: 1px solid #666;
  color: #666;
}

.btn--white:hover{
  color: white;
}

.btn--black{
  border: 1px solid #a9a9a9;
  color: #d6d6d6;
}

.btn--blue{
  color: white;
  background-color: #2d66f7;
  border: 1px solid #2d66f7;
}

.btn--blue:hover {
  background-color: transparent;
  color: #2d66f7;
}

.btn--center{
  margin-top: 100px;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}

/*CART*/
.cart-detail{
  display: none;
  position: absolute;
  top: 34px;
  right: 0px;
  background: #1f1f1f;
  width: 300px;
  padding: 10px 20px 20px;
  cursor: default;
}

.cart:hover > .cart-detail{
  display: block;
}

.cart-list{

}

.cart-content{
  display: flex;
}

.cart-items{
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding: 12px 0;
}

.cart-img{
  width: 50px;
}

.cart-items__info{
  margin-left: 18px;
  color: white;
}

.cart-items__name{
  font-size: 0.75rem;
  font-weight: lighter;
  text-transform: capitalize;
}

.cart-items__quantity{
  font-size: 0.7rem;
  font-weight: lighter;
  color: #999;
  margin-top: 3px;
}

.cart-items__number{

}

.cart-items__times{

}

.cart-items__price{

}

.cart-items__delete{
  color: #999;
  font-size: 0.8rem;
  padding: 0px 5px;
  background-color: #333;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.2s linear;
}

.cart-items__delete:hover{
  color: white;
}

.cart-total{
  color: white;
  align-items: center;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #555;
  padding: 10px 0;
}

.cart-total__words{
  font-size: 0.7rem;
  text-transform: uppercase;
  font-weight: bold;
}
.cart-total__price{
  font-size: 0.7rem;
  font-weight: bold;
}

.cart-footer{
  padding-top: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.cart-footer__view-cart{
  color: #999;
  text-decoration: none;
  text-transform: capitalize;
}

.btn.cart-footer__btn{
  padding: 10px 15px;
  color: white;
  font-size: 0.7rem;
  background: #2d66f7;
  border: none;
}

.btn.cart-footer__btn:hover{
  background-color: #999;
  transition: all 0.4s linear;
}

.cart-footer__icon{
  margin-right: 4px;
}

.cart-footer__btn-words{

}


/*FEATURE SECTION*/
.feature-section{
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  padding: 110px 62px;
  overflow-x: hidden;
  width: 100%;
  overflow-y: hidden;
}

.white {
  background-color: white;
}

.black{
  background-color: #232323;
}

.feature-content{
  max-width: 480px;
  min-width: 400px;
  padding: 0 15px;
}

.feature-content__heading{
  font-size: 2.2rem;
  margin-bottom: 30px;
}

.black .feature-content__heading{
  color: white;
}

.feature-content__font{
  font-family: 'Playfair Display', serif;

}

.feature-content__summary{
  font-family: 'Playfair Display', serif;
  margin-bottom: 30px;
  font-size: 1.25rem;
  font-weight: lighter;
  line-height: 1.5;
}

.white .feature-content__summary{
  color: #666;
}

.black .feature-content__summary{
  color: #a9a9a9;
}

.feature-content__description{
  margin-bottom: 30px;
  font-size: 0.9rem;
  color: #666;
  line-height: 1.7;
}

.white .feature-content__description{
  color: #666;
}

.black .feature-content__description{
  color: #a9a9a9;
}

.feature-content__wrapper{
  /*width: 100%;*/
  /*height: 100%;*/
  /*display: flex;*/
  /*align-items: center;*/
}

.feature-img{
  width: 600px;
}

/*HERO SECTION*/
.hero-section{
  background-color: #f9f9f9;
  padding: 40px 62px 80px;
  width: 100%;
}

.hero__heading{
  margin: 20px 0 60px;
  font-size: 1.5rem;
  font-weight: bold;
}

.hero__grid{
  display: grid;
  grid-template-columns: repeat(3, minmax(250px, 1fr));
  grid-template-rows: repeat(3,1fr);
  grid-gap: 30px;
}

.hero-items{
  width: 100%;
  position: relative;
  height: 100%;
  overflow: hidden;
}

.hero-items__img{
  width: 100%;
  height: 100%;
  transition: all 0.4s ease-out;
}

.hero-items__overlay{
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  text-align: center;
  width: 100%;
  height: 100%;
  line-height: 14;
  background-color: rgba(0,0,0,0.8);
  opacity: 0;
  cursor: pointer;
  transition: all 0.4s ease-out;
  display: flex;
  align-items: center;
  justify-content: center;
}

.hero-items:hover .hero-items__overlay{
  opacity: 1;
}

.hero-items:hover .hero-items__img{
  transform: scale(1.1);
  transition: all 0.4s ease-out;
}

.hero-items__description{
  color: white;
  font-size: 0.9rem;
  font-weight: bold;
}

/*DISCOVER SECTION*/
.discover-section{
  background-color: white;
  width: 100%;
  padding: 62px 80px;
}

.discover-section__heading{
  text-align: center;
  font-size: 2.1rem;
  text-transform: capitalize;
  margin-bottom: 30px;
}

.discover-section__summary{
  text-align: center;
  width: 60%;
  margin: auto;
  font-size: 1.2rem;
  color: #666;
  font-weight: lighter;
}

.discover-section__grid{
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-gap: 10px 30px;
  margin-top: 60px;
}

.discover-items{
  display: flex;
  align-items: flex-start;
  margin-bottom: 30px;
}

.discover-items__icon{
  margin-top: 4px;
  font-size: 1.2rem;
  color: #2d66f7;
  min-width: 24px;
}

.discover-items__info{
  margin-left: 19px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.discover-items__name{
  font-size: 1.1rem;
  margin-bottom: 16px;
}

.discover-items__description{
  font-size: 0.9rem;
  color: #666;
}

/*Footer*/
.footer-section{
  background-color: #232323;
  padding: 85px 100px;
}

.footer-content{
  display: grid;
  grid-template-columns: repeat(4,1fr);
  grid-gap: 30px;
  padding-bottom: 80px;
  border-bottom: 1px solid #333;
  grid-gap: 30px;
}

.footer-content__items{
  color: #999;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.footer-content__heading{
  font-size: 0.9rem;
  color: white;
  letter-spacing: 1px;
  text-transform: uppercase;
  margin-bottom: 16px;
}

.footer-content__words{
  color: #999;
  font-size: 0.8rem;
  line-height: 2;
}

.footer-content__links{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}

.footer-content__links-items{
  font-size: 0.8rem;
  color: #666;
  margin-bottom: 6px;
  cursor: pointer;

  &:hover{
    color: #fff;
    .footer-content__links-items-name{
      color: #fff;
    }
  }
}

.footer-content__links-items-name{
  font-size: 0.8rem;
  color: #666;
  margin-bottom: 6px;
  cursor: pointer;
  transition: all 0.3s linear;
}

.footer-content__links-items-time{
  font-size: 0.6rem;
  font-weight: bold;
  transition: all 0.1s ease-in-out;
}

.footer-content__email{
  background-color: transparent;
  padding: 13px 0;
  margin-bottom: 20px;
  outline: none;
  border-width: 0 0 1px 0;
  letter-spacing: 1px;
  color: #999;
  font-size: 0.8rem;
  transition: all 0.2s linear;

  &:focus{
    border-color: white;
    color: white;

    &::placeholder{
      color: white;
    }
  }
}

.footer-content__btn{
  color: #fff;
  border: 1px solid #333;
  background: #333;
  width: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer-content__warning{
  font-size: 0.6rem;
  color: #666;
  margin-left: 8px;
  margin-top: 6px;
}

.footer-contact{
  display: flex;
  justify-content: space-between;
  margin-top: 32px;
}

.footer-contact__left{
  color: #999;
  font-size: 0.7rem;
  line-height: 2;
}

.footer-contact__words{
  color: #999;
  font-size: 0.8rem;
  line-height: 2;
}

.footer-contact__links{
  cursor: pointer;
  transition: all 0.2s ease;
  &:hover{
    color: white;
  }
}

.footer-contact__right{

}

.footer-contact__icons{
  font-size: 1.5rem;
  color: #666;
  cursor: pointer;
  margin: 0 5px;
  transition: all 0.2s ease;
  &:hover{
    color: white;
  }
}
